import React from "react";
import nav from "./NavBar.module.css";
import { useRef } from 'react';
import { useState } from "react";
import AnchorLink from "react-anchor-link-smooth-scroll";
import resume from "../../assets/Resume.pdf"


function NavBar(){
    
    const[menu, setMenu] = useState("home");
        const menuref = useRef();
        const openMenu = () =>{
            menuref.current.style.right = "0";
        }
        const closeMenu = () =>{
            menuref.current.style.right = "-950px";
            menuref.current.style.display = "none";
        }

        const downloadFileName = "Shreya_Redkar_Full-Stack-developer_CV"

        return(
           
            <div className={nav.navBar}>
                <h1 className={nav.title}>Profile</h1>
                <img src="https://cdn-icons-png.flaticon.com/128/8727/8727897.png" alt="" className={nav.mobOpen} onClick={openMenu}/>
                <ul ref={menuref} className={nav.navList}>
                    <img src="https://cdn-icons-png.flaticon.com/128/5369/5369422.png" alt="" className={nav.mobClose} onClick={closeMenu}/>
                    <li><AnchorLink className={nav.anchorLink} href="#home"><p onClick={()=>setMenu("home")} className={menu === "home"? nav.selectedMenu : nav.menu}>Home</p></AnchorLink></li>
                    <li><AnchorLink className={nav.anchorLink} offset={50} href="#about"><p onClick={()=>setMenu("about")} className={menu === "about"? nav.selectedMenu : nav.menu}>About me</p></AnchorLink></li>
                    <li><AnchorLink className={nav.anchorLink} offset={50} href="#experience"><p onClick={()=>setMenu("experience")} className={menu === "experience"? nav.selectedMenu : nav.menu}>Work Experience</p></AnchorLink></li>
                    <li><AnchorLink className={nav.anchorLink} offset={50} href="#education"><p onClick={()=>setMenu("education")} className={menu === "education"? nav.selectedMenu : nav.menu}>Education</p></AnchorLink></li>
                    <li><AnchorLink className={nav.anchorLink} offset={50} href="#skills"><p onClick={()=>setMenu("skills")} className={menu === "skills"? nav.selectedMenu : nav.menu}>Skills</p></AnchorLink></li>
                    <li><AnchorLink className={nav.anchorLink} offset={50} href="#contact"><p onClick={()=>setMenu("contact")} className={menu === "contact"? nav.selectedMenu : nav.menu}>Contact me</p></AnchorLink></li>
                </ul>
                <div className={nav.resumeButton}><a href={resume} download={downloadFileName}>Download Resume</a></div>
            </div>
        )
}

export default NavBar;